.footer__copyright {
  margin: 0;
  font-family: "Inter", "Arial", "Helvetica", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #545454;
}

@media (max-width: 900px) {
  .footer__copyright {
    padding-left: 19px;
  }
}

@media (max-width: 500px) {
  .footer__copyright {
    font-size: 14px;
    line-height: 17px;
  }
}
